<template>
    <div class="row">
        <div class="col-sm-12">
            <b-jumbotron bg-variant="light" border-variant="dark">
                <b-btn v-b-toggle="getId(phase)" variant="success" size="lg">{{phase.name}}</b-btn>
                <b-collapse :id="getId(phase)" class="mt-2">
                    <ability v-for="ability in phase.abilities" :key="ability.name" :ability="ability">
                    </ability>
                </b-collapse>
            </b-jumbotron>
        </div>
    </div>
</template>

<script>
import ability from "./ability.vue";
export default {
  name: "phase",
  props: {
    phase: {
      type: Object,
      required: true
    }
  },
  methods: {
    getId(ability){
        return ability.name.replace(new RegExp(' ', 'g'), '-');
    }
  },
  components: {
    ability
  }
};
</script>