<template>
    <nav>
        <div v-if="error" class="error">
      {{ error }}
    </div>
    <h1>{{raid.name}}</h1>
    <ul class="nav nav-tabs">
        <!--<router-link :v-for="boss in raid.bosses" :key="boss.name" :to="{name: 'boss', params: boss.name}" tag="li">{{ boss.name }}</router-link>-->
        <li v-for="boss in raid.bosses" :key="boss.name"><router-link to="/uldir/boss/Taloc">{{boss.name}}</router-link></li>
    </ul>
    </nav>
</template>

<script>
import { getRaid } from "../api/raids.js";
export default {
  name: "navigation",
  data() {
    return {
      loading: false,
      raid: {
        bosses: [""]
      },
      error: null
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      getRaid("uldir", (err, raid) => {
        this.error = this.raid = null;
        this.loading = false;
        if (err) {
          this.error = err.toString();
        } else {
          this.raid = raid;
        }
      });
    }
  }
};
</script>

