<template>
    <section class="row">
        <div class="loading" v-if="loading">Loading...</div>
        <div v-if="error" class="error">
            {{ error }}
        </div>
        
        <div class="col-sm-12">
            <h2>{{boss.name}}</h2>
            <ul>
                <li v-for="link in boss.links" :key="link"><a :href="link">{{link}}</a></li>
            </ul>
        </div>
        <div class="col-sm-12">
            <phase v-for="phase in boss.phases" :key="phase.name" :phase="phase">
            </phase>
        </div>
    </section>
</template>

<script>
import phase from "./phase.vue";
import { getBoss } from "../api/raids.js";
export default {
  name: "boss",
  data() {
    return {
      loading: false,
      boss: null,
      error: null
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      getBoss(this.$route.params.raid, this.$route.params.name, (err, boss) => {
        this.error = this.raid = null;
        this.loading = false;
        if (err) {
          this.error =
            err.toString() +
            " " +
            this.$route.params.raid +
            " " +
            this.$route.params.name;
        } else {
          this.boss = boss;
        }
      });
    }
  },
  components: {
    phase
  }
};
</script>

