<template>
    <b-jumbotron bg-variant="light" text-variant="black" border-variant="dark">
        <b-btn v-b-toggle="getId(ability)" variant="success" size="lg">{{ability.name}}</b-btn>
        <div class="right">
            <img v-for="role in ability.roles" :key="role" :class="role" />
        </div>
        <b-collapse :id="getId(ability)" class="mt-2">
            <h4>{{getUtilities(ability)}}</h4>
            <p><a :href="ability.wowheadLink" target="_blank">Wowhead</a></p>
            <p v-if="ability.timestamp"><a :href="ability.timestamp" target="_blank">Video Timestamp</a></p>
            <p>{{ability.description}}</p>
            <p>Appears in: {{getDifficultiesAppearing(ability)}}</p>

            <video controls="controls" v-if="ability.video">
                <source :src="ability.video" type="video/mp4" />
            </video>
        </b-collapse>
    </b-jumbotron>
</template>

<script>
export default {
  name: "ability",
  props: {
    ability: {
      type: Object,
      required: true
    }
  },
  methods: {
    getDifficulty(difficulty) {
      switch (difficulty) {
        case 1:
          return "Normal";
        case 2:
          return "Heroic";
        case 3:
          return "Mythic";
      }
    },
    getDifficultiesAppearing(ability){
        var difficulties = [];
        for(var i = ability.minDifficulty; i <= ability.maxDifficulty; i++){
            difficulties.push(this.getDifficulty(i));
        }
        return difficulties.join(", ");
    },
    getUtilities(ability){
        return ability.utility.join(", ")
    },
    getId(ability){
        return ability.name.replace(new RegExp(' ', 'g'), '-');
    }
  }
};
</script>

<style scoped>
    video {
        width: 100%;
    }

    .healer{
        background-image: url('/sprites.png');
        background-repeat: no-repeat;
        width: 70px;
        height: 70px;
        background-position-x: -65px;
        background-position-y: 3px;
    }

    .tank{
        background-image: url('/sprites.png');
        background-repeat: no-repeat;
        width: 70px;
        height: 70px;
        background-position-y: -65px;
    }

    .dps{
        background-image: url('/sprites.png');
        background-repeat: no-repeat;
        width: 70px;
        height: 70px;
        background-position-x: -65px;
        background-position-y: -65px;
    }

    .right{
        float: right;
    }
</style>
